.example {
  background-color: #ffc107;
}

.boxStylez {
  width: 333px;
  border: 5px solid white;
  margin: 0 auto;
  height: 330px;
  overflow: hidden;
}

.boxStylez .bsPrice {
  text-align: right;
  margin-top: 0;
  color: orange;
}

.boxStylez .bsText {
  margin-bottom: 0;
  font-weight: 100;
  color: #300804;
}

.boxStylez .bsInnerInner {
  height: 250px;
  overflow: hidden;
}

.boxStylez .bsInnerInner .boxStylezInner {
  width: 100%;
  height: 250px;
  background-repeat: no-repeat;
  background-size: cover;
  -moz-transition: all 0.3s;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

.boxStylez .bsInnerInner .boxStylezInner:hover {
  -moz-transform: scale(1.1);
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}

@-webkit-keyframes floating-arrow {
  from {
    -webkit-transform: translateY(-4px);
    transform: translateY(-4px);
  }

  65% {
    -webkit-transform: translateY(4px);
    transform: translateY(4px);
  }

  to {
    -webkit-transform: translateY(-4px);
    transform: translateY(-4px);
  }
}

@keyframes floating-arrow {
  from {
    -webkit-transform: translateY(-4px);
    transform: translateY(-4px);
  }

  65% {
    -webkit-transform: translateY(4px);
    transform: translateY(4px);
  }

  to {
    -webkit-transform: translateY(-4px);
    transform: translateY(-4px);
  }
}

@-webkit-keyframes floating-arrow-up {
  0% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }

  25% {
    -webkit-transform: translateY(4px);
    transform: translateY(4px);
  }

  75% {
    -webkit-transform: translateY(-4px);
    transform: translateY(-4px);
  }
}

@keyframes floating-arrow-up {
  0% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }

  25% {
    -webkit-transform: translateY(4px);
    transform: translateY(4px);
  }

  75% {
    -webkit-transform: translateY(-4px);
    transform: translateY(-4px);
  }
}

.mbrArrowFloating {
  bottom: 48px;
  left: 0;
  position: absolute;
  text-align: center;
  width: 100%;
}

.mbrArrowFloating .mbrArrowIcon {
  -webkit-animation: floating-arrow 1.6s infinite ease-in-out 0s;
  animation: floating-arrow 1.6s infinite ease-in-out 0s;
  background: white;
  border-radius: 3px;
  color: orange;
  display: inline-block;
  height: 46px;
  outline-style: none !important;
  position: relative;
  text-decoration: none;
  transition: all .3s ease-in-out;
  width: 46px;
}

.mbrArrowFloating .mbrArrowIcon::before {
  content: "\203a";
  display: inline-block;
  font-family: serif;
  font-size: 44px;
  line-height: 1;
  font-style: normal;
  left: 4px;
  position: relative;
  top: 6px;
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
}




